import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import useMarvelService from '../../services/MarvelService';
import Spinner from '../spinner/Spinner';
import ErrorMessage from '../errorMessage/ErrorMessage';

import './comicsList.scss';


const ComicsList = () => {

    console.log('re-render')
    const [comicsList, setComicsList] = useState([]);
    const {loading, error, getAllComics, clearError} = useMarvelService();
    const [offset, setOffset] = useState(10500);
    const [newItemLoading, setNewItemLoading] = useState(true)

    useEffect(() => {
        if (!localStorage.getItem('data')) {
            onRequest();
        }
        else {
            setComicsList(JSON.parse(localStorage.getItem('data')));
            setNewItemLoading(false)
        }
    }, [])

    const onRequest = (offset) => {
        clearError();
        setNewItemLoading(true);
        getAllComics(offset)
            .then(onComicsLoaded)
    }
    
    const onComicsLoaded = (newComics) => {
        localStorage.setItem('data', JSON.stringify([...comicsList, ...newComics]));
        console.log('started')
        setNewItemLoading(false)
        setComicsList(comics => [...comics, ...newComics]);
        console.log(comicsList)
        // localStorage.setItem('data', JSON.stringify([...comicsList, ...newComics])); //не будет работать, так как в рамках одной функции мы имеем только старые данные состояний
        setOffset(offset => offset + 8);
    }

    function renderComics(comics) {
        console.log(comics)
        const items = comics.map(({title, thumbnail, price, id}, i) => {
            return (
                <li 
                    className="comics__item"
                    key={i}>
                    <Link to={`/comics/${id}`}>
                        <img src={thumbnail} alt={title} className="comics__item-img"/>
                        <div className="comics__item-name">{title}</div>
                        <div className="comics__item-price">{price}</div>
                    </Link>
                </li>
            )
        })
        return (
            <ul className="comics__grid">
                {items}
            </ul>
        )
    }

    const items = renderComics(comicsList);
    const spinner = loading ? <Spinner/> : null;
    const errorMessage = error ? <ErrorMessage/> : null;

    return (
        <div className="comics__list">
            {items}
            {spinner}
            {errorMessage}
            <button 
                className="button button__main button__long"
                onClick={() => onRequest(offset)}
                disabled={newItemLoading}>
                <div className="inner">load more</div>
            </button>
        </div>
    )
}

export default ComicsList;